@import '../../styles/color-palette';

.pill-menu {
  display: flex;
  height: 80%;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  padding: 2px;
  border: 0;
  margin-left: auto;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 23px;
  color: $color-white-1;
  cursor: pointer;
  font-size: 14px;
  letter-spacing: 0.015em;
  line-height: 20px;
  outline: none;

  @media (max-width: 768px) {
    width: 10%;
  }

  &:hover {
    background-color: $color-black-5;
  }
}

.image {
  position: relative;
  display: inline-block;
  overflow: hidden;
  width: 28px;
  height: 28px;
  border-radius: 50%;
}

.text {
  overflow: hidden;
  max-width: 110px;
  margin-right: 8px;
  margin-left: 8px;
  font-weight: 700;
  pointer-events: none;
  text-overflow: ellipsis;
  white-space: nowrap;

  @media (max-width: 768px) {
    display: none;
  }
}

.dropdown-icon {
  width: 10px;
  height: 5px;
  margin-right: 8px;

  @media (max-width: 768px) {
    display: none;
  }
}

.pill-menu-list {
  margin-top: 40px;
}

.list-text {
  margin-right: 8px;
  margin-left: 8px;
  color: $color-gray-3;
  font-size: 14px;
}

// Overrule Material-UI List Paddings
.MuiList-padding {
  /* stylelint-disable-next-line declaration-no-important */
  padding-top: 0 !important;
  /* stylelint-disable-next-line declaration-no-important */
  padding-bottom: 0 !important;
}
